import * as React from "react"
import "./about.scss"
const About = () => {
   const btnRef = React.useRef(null)
   const [translatevalue, settranslatevalue] = React.useState({ x: 0, y: 0 })

   const detecEnterMouse = (e) => {
      const { x, y, top, left, width, height } = document.querySelector(".btn-parallax").getBoundingClientRect()
      const midleBtnPointInitial = { xMiddle: x / 2, yMiddle: y / 2 }
      Object.freeze(midleBtnPointInitial)
      e.target.addEventListener("mousemove", (event) => {
         console.log(Object.isFrozen(midleBtnPointInitial));
         settranslatevalue({ x: event.clientX - midleBtnPointInitial.xMiddle, y: event.clientY - midleBtnPointInitial.yMiddle })

      })

   }
   return (
      <div>
         <h1>About Page</h1>
         <div className={"btn-parallax"} style={{ transform: `translate3d(${translatevalue.x}px, ${translatevalue.y}px, 0px)` }} ref={btnRef} onMouseEnter={(e) => detecEnterMouse(e)}></div>
      </div>
   )
}

export const Head = () => <title>About page</title>
export default About